@import '../../styles/constants.scss';
@import '../../styles/mixins.scss';

.intro {
    @include flex;
    width: 100%;
    height: 100vh;

    &__left {
        flex: 1;
        margin-left: 100px;
        gap: 2rem;

        .me {
    
            &>:nth-child(3) {
                font-weight: 100;
                font-size: 32px;
                color: $gray;
                margin-top: 35px;
                margin-bottom: 20px;
                max-width: 600px;
                line-height: 20px;
                margin-bottom: 2rem;
                opacity: 0.8;
            }
        }
        .link {
            margin-top: 10rem;
        }
    }

    &__right {
        flex: 1;
        position: relative;

        &>*{
            position: absolute;
            z-index: 1;
        }

        .blur {
            top: -180%;
            left: 60%;
        }

        .cat {
            width: 200px;
            right: 5rem;
            transform: rotate(-90deg);
        }
    }
}